*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
body{
  color: #333;
  line-height: 1;
  background-color: bisque;
  font-family: 'UhBeeJJIBBABBA';
  letter-spacing: 4px;

}
.list{
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: aliceblue;
  border-radius: 20px;
  border: 3px solid green;
  text-align: center;
  font-size: 20px;
}
.list form{
  position: relative;
}
.list input{
  border: none;
  background-color: #3333338c;
  backdrop-filter: blur(10px);
  height: 48px;
  border-radius: 20px;
  width: 400px;
  margin: 30px 0;
  font-family: 'UhBeeJJIBBABBA';
  padding: 0 20px;
  font-size: 20px;
  letter-spacing: 4px;


}
.list ul li{
  background-color: #a5a5a5;
  border-bottom: 2px solid #fff;
  height: 50px;
  border-radius: 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

}
.list ul li span::after{
  content: "ðŸš½";
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list h2{
  margin-top: 30px;
}
.list input::placeholder{
  color: #fff;
  font-size: 20px;
  font-weight: 900;
  padding: 0 20px;
  font-family: 'UhBeeJJIBBABBA';
  letter-spacing: 4px;


}
.list button{
  background-color: #a5a5a5;
  border: 2px solid #333;
  color: #333;
  height: 50px;
  width: 100px;
  border-radius: 20px;
  position: absolute;
  right: 100px;
  top: 50%;
  transform: translateY(-50%);
}
li:nth-child(even)::before{
  position: absolute;
  content: 'ðŸ’‰';
  width: 20px;
  height: 20px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
li:nth-child(odd)::before{
  position: absolute;
  content: 'ðŸ©¸';
  width: 20px;
  height: 20px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.list ul li:nth-child(even){
  background-color: aliceblue;
}
